body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.everything {
  width: 100vw;
  height:100vh;
  background-color: black;
  color: #FFD700;
  text-align: center;
}

.title {
  font-size: 80px;
}

.pictures {
  text-align: left;
  height: 30vh;
  width:100vw;
    position: absolute;
  background-color: transparent;

}

.picture {
  height:100%;

}
.pictureS {
  position:absolute;
  height:90%;
  right:5vw;

}


.question {
  position: relative;
  top:10vh;
  font-size: 30px;
}
.failed {
  position: relative;
  top:10vh;
  font-size: 20px;
}


.input_field {
  background-color: black;
  border: none;
  height:15vh;
  width:30%;
  font-size:25px;
  text-align: center;
  color: #FFD700;
  position: relative;
  top: 30vh;

  border: 1px solid transparent;
  border-left: none; 
  padding: 15px;
  -webkit-border-image: url(/sw_api/static/media/border.83911c3d.png)1 stretch; /* Safari 3.1-5 */


}

::-webkit-input-placeholder {
  color: #FFD700;
  font-size:25px;
}

::-ms-input-placeholder {
  color: #FFD700;
  font-size:25px;
}

::placeholder {
  color: #FFD700;
  font-size:25px;
}
:focus {
  outline: none;
}
